//A component is just a function which return jsx template and exported bottom 
//of the  function
import { Link } from 'react-router-dom';
import React from 'react';
const Namvbar = () => {
    return (
        <div className="navbar">
            <h1>The Rahul`s Blog</h1>
            <div className="links">
                <Link to="/">Home</Link>
                <Link to="/create" className="newBlog">New Blog</Link>
            </div>
        </div>
    );
}

export default Namvbar;